    setDataTable() {
      if (this.currentInfo === 1) {
        window.$("#price-less-table").DataTable({
          columnDefs: [{ orderable: false, targets: [3, 4, 5, 6, 7] }],
          ordering: true,
          searching: false,
          paging: false
        });
      }
    },
    changeList() {
      this.importantList = list[this.currentInfo];
      setTimeout(() => {
        this.setDataTable();
      }, 1000);
    }
  },
  mounted() {
    window.$('[data-toggle="tab"]').on('shown.bs.tab', (e) => {
      let li = window.$(e.target).closest('.nav-item')
      this.currentTab = e.target.dataset.parent
      this.currentInfo = e.target.dataset.item
      this.changeList()
      if (li.className == "dropdown-item") {
        let link = li.querySelector('a')
        window.$(e.target).attr('aria-selected', false)
        window.$(e.target).removeClass('active show')
        window.$(link).addClass('active show')
      }
    });
  }
};
